@import url(https://fonts.googleapis.com/css2?family=Teko:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Teko:wght@300&display=swap);
.home {
  font-family: "Teko", sans-serif;
  font-size: 2.2em;
  width: 60%;
  justify-content: center;
  align-items: center;
  margin: auto;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

.searchBar {
  display: flex;
  border: 1px solid #8888;
  width: 10em;
  border-radius: 1em;
}

.searchBar_icon {
  padding: 6px 10px;
  margin-top: 8px;
  margin-right: 16px;
  font-size: 17px;
  border: none;
  cursor: pointer;
}

.searchBar_title input {
  padding: 6px;
  margin-top: 8px;
  font-size: 17px;
  border: none;
  outline: none;
}

.todoList {
  display: flex;
  border: 1px solid black;
  padding-left: 0.2em;
}

.todo {
  width: 20rem;
}

.todoDetails {
  display: flex;
  border: 1px solid black;
  padding-left: 0.2em;
  align-items: center;
}

.todo {
  width: 20rem;
  padding-left: 1em;
  font-size: 1.5rem !important;
}

.todoButton {
  padding: 5px;
  border: 1px solid #8888;
  width: 5em;
  text-align: center;
}

.user {
  font-family: "Teko", sans-serif;
  font-size: 2rem;
  width: 90%;
  justify-content: center;
  align-items: center;
  margin: auto;
}

.userList {
  display: flex;
  flex-direction: column;
  border: 1px solid black;
  padding-left: 0.2em;
}

.user {
  width: 40rem;
  padding: 4px;
}

.userDetails {
  display: flex;
  border: 1px solid black;
  padding-left: 0.2em;
  align-items: center;
}

.user {
  width: 20rem;
  padding-left: 1em;
  font-size: 1.5rem !important;
}

